'use strict';
import '../../style/FacebookButton.css';
import axios from 'axios'
import React from 'react';
import FacebookLogin from 'react-facebook-login';


class FacebookButton extends React.Component {
    responseFacebook(response) {
        if (response.hasOwnProperty('authResponse')
            && response['authResponse'].hasOwnProperty('accessToken')) {

            axios.post('/facebook/token/', {'access_token': response['authResponse']['accessToken']})
                .then(function (response) {
                        console.log(response);
                    }
                );
        }
    }

    render() {
        return (
            <FacebookLogin
                appId="269457056812449"
                autoLoad={true}
                cssClass="FacebookButton"
                fields="name, email, picture"
                icon="fa-facebook"
                scope="public_profile, user_friends, user_actions"
                callback={this.responseFacebook}
            />
        )
    }
}

export default FacebookButton;


